}

.folder-group img {
  width: 100px;
}

.folder-group li {
  text-align: center;
  margin-right: 10px;
  margin-left: 10px;
  margin-top: 20px;
  max-width: 140px;
}
.folder-group .row {
  justify-content: center;
}

