  color: grey;
}

.noResultFound img {
  width: 300px;
  display: block;
  margin-top: 10px;
}

@media screen and (max-width: 475px) {
  .noResultFound img {
    width: 200px;
  }
}

@media screen and (max-width: 287px) {
  .noResultFound img {
    width: 184px;
  }
}

