} */

.modal-footer {
  background-color: #f9f9f9;
}

.con {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
  /* background: #05091f; */
}

.con svg {
  position: relative;
  width: 150px;
  height: 150px;
  animation: rotate 1.5s linear infinite;
}

@keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}

.con svg circle {
  width: 100%;
  height: 100%;
  fill: none;
  stroke-width: 3;
  stroke-linecap: round;
  stroke: #000;
  /* stroke: #00a1ff; */
  transform: translate(5px, 5px);
  stroke-dasharray: 110;
  stroke-dashoffset: 110;
  animation: animate 2.5s infinite;
}

@keyframes animate {
  0%,
  100% {
    stroke-dashoffset: 110;
  }
  50% {
    stroke-dashoffset: 0;
  }
  50.1% {
    stroke-dashoffset: 220;
  }
}
